import { useMemo } from 'react';
import { getStateColumnActions } from '../angular/doc_table/actions/columns';
export const useDataGridColumns = ({
  capabilities,
  config,
  indexPattern,
  indexPatterns,
  setAppState,
  state,
  useNewFieldsApi
}) => {
  const {
    onAddColumn,
    onRemoveColumn,
    onSetColumns,
    onMoveColumn
  } = useMemo(() => getStateColumnActions({
    capabilities,
    config,
    indexPattern,
    indexPatterns,
    setAppState,
    state,
    useNewFieldsApi
  }), [capabilities, config, indexPattern, indexPatterns, setAppState, state, useNewFieldsApi]);
  const columns = useMemo(() => {
    if (!state.columns) {
      return [];
    }

    return useNewFieldsApi ? state.columns.filter(col => col !== '_source') : state.columns;
  }, [state, useNewFieldsApi]);
  return {
    columns,
    onAddColumn,
    onRemoveColumn,
    onMoveColumn,
    onSetColumns
  };
};