import { uniq } from 'lodash';
export const buildPointSeriesData = (table, dimensions) => {
  const {
    x,
    y
  } = dimensions;
  const xAccessor = table.columns[x.accessor].id;
  const yAccessor = table.columns[y.accessor].id;
  const chart = {};
  chart.xAxisOrderedValues = uniq(table.rows.map(r => r[xAccessor]));
  chart.xAxisFormat = x.format;
  chart.xAxisLabel = table.columns[x.accessor].name;
  chart.yAxisFormat = y.format;
  const {
    intervalESUnit,
    intervalESValue,
    interval,
    bounds
  } = x.params;
  chart.ordered = {
    date: true,
    interval,
    intervalESUnit,
    intervalESValue,
    min: bounds.min,
    max: bounds.max
  };
  chart.yAxisLabel = table.columns[y.accessor].name;
  chart.values = table.rows.filter(row => row && row[yAccessor] !== 'NaN').map(row => ({
    x: row[xAccessor],
    y: row[yAccessor]
  }));
  return chart;
};